<template>
    <div class="loanCalc">
        <div class="block select">
            <p class="title">借款金额(元)</p>
            <div class="selects">
                <div
                    class="option text-center"
                    :class="{active: item == sel_money}"
                    :style="{background: item == sel_money ? appConfig.appColor : '#f5f5f5'}"
                    v-for="(item, index) in money_option"
                    :key="index"
                    @click="sel_money = item"
                >{{item}}</div>
                <p class="tip text-center">- 按日计息,次日起可提前还款,免违约金 -</p>
            </div>
            <hr>
            <p class="title sec-title">借款期限</p>
            <div class="selects">
                <div
                    class="option text-center"
                    :class="{active: item == sel_time}"
                    :style="{background: item == sel_time ? appConfig.appColor : '#f5f5f5'}"
                    v-for="(item, index) in time_option"
                    :key="index"
                    @click="sel_time = item"
                >{{item}}</div>
            </div>
        </div>
        <div class="block plan">
            <p class="title flex">
                <span>期数</span>
                <span>还款额</span>
            </p>
        </div>
        <div
            class="block flex interest"
            v-for="(item, index) in loanTime"
            :key="index"
            :class="{first: index == 0}"
        >
            <div class="time">
                <div>第{{index + 1}}期</div>
                <div :style="[appConfig.mainColor]">{{item.time}}</div>
            </div>
            <div class="money">
                <div
                    :style="[appConfig.mainColor]"
                >￥{{(Math.round(item.capital * 100) + Math.round(item.interest*100))/100}}</div>
                <div class="math">(本金+息费)￥{{item.capital}}+￥{{item.interest}}</div>
            </div>
        </div>
        <div class="inter block">
            <img
                src="https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181203/icon%402x.png"
                alt
            >
            总息费
            <span
                :style="[appConfig.mainColor]"
            >￥{{Math.round(totalInterest * 100) / 100}}</span>
        </div>
        <div class="inter block">
            <img
                src="https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181203/icon%402x.png"
                alt
            >年息24%，日息最低0.06%
        </div>
        <div class="next block">
            <cube-button
                class="code-btn"
                @click="nextStep"
                :style="[appConfig.mainBackground]"
                :primary="true"
            >下一步</cube-button>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    data() {
        return {
            money_option: [3000, 5000, 10000, 20000, 30000, 50000],
            sel_money: parseInt(this.$route.query.money) || 10000,
            time_option: [3, 6, 12],
            sel_time: parseInt(this.$route.query.time) || 12,
            totalInterest: 0, // 总息费
            loanTime: []
        }
    },
    computed: {
        ...mapState(['appConfig', 'user'])
    },
    methods: {
        isLeapYear(Year) {
            // 是否闰年
            if ((Year % 4 == 0 && Year % 100 != 0) || Year % 400 == 0) {
                return true
            } else {
                return false
            }
        },
        dateList(time_option) {
            // 根据期数生成对应的还款时间和每期的天数
            const now = new Date()
            let Year = now.getFullYear()
            let Day = now.getDate()
            let Month = now.getMonth() + 1
            let obj = []
            let lists = []
            for (let i = 1; i < time_option + 1; i++) {
                let m_day
                if (Month === 12) {
                    Month = 1
                    Year++
                } else {
                    Month++
                }
                if (Day > 28 && Month === 2) {
                    m_day = this.isLeapYear(Year) ? 29 : 28
                } else if (Day > 30 && [2, 4, 6, 9, 11].includes(Month)) {
                    m_day = 30
                } else {
                    m_day = Day
                }
                lists.push(Year + '/' + Month + '/' + m_day)
            }
            for (let i = 0; i < lists.length; i++) {
                let times // 天数
                if (i == 0) {
                    times = (Date.parse(lists[0]) - Date.parse(now.toLocaleDateString())) / (24 * 60 * 60 * 1000)
                } else {
                    times = (Date.parse(lists[i]) - Date.parse(lists[i - 1])) / (24 * 60 * 60 * 1000)
                }
                obj.push({ time: lists[i], days: times })
            }
            return obj
        },
        countMoney() {
            // 生成期数和对应的本金和息金
            this.totalInterest = 0
            const capital = (Math.ceil((this.sel_money * 100) / this.sel_time) / 100).toFixed(2) //每期本金
            const interest = day => {
                return (Math.ceil(((this.sel_money * 100 * 0.24) / 365) * day) / 100).toFixed(2) // 利息
            }
            const times = this.dateList(this.sel_time)
            for (let i = 0; i < times.length; i++) {
                times[i].interest = +interest(times[i].days)
                this.totalInterest += +interest(times[i].days)
                times[i].capital = +capital
            }
            this.loanTime = times
        },
        nextStep() {
            if (this.user.certification.person && this.user.certification.img && this.user.certification.bank) {
                this.$router.push(`/loanCalcConfirm?money=${this.sel_money}&time=${this.sel_time}`)
                return
            } else {
                this.$router.push('/creditCertification')
            }
            // this.$router.push(`/loanCalcConfirm?money=${this.sel_money}&time=${this.sel_time}`)
        }
    },
    mounted() {
        this.countMoney()
    },
    beforeCreate() {
        document.title = '贷款计算'
    },
    beforeRouteEnter(to, from, next) {
        if (from.path == '/creditCertification') {
            next(vm => {
                if (vm.user.certification.person && vm.user.certification.img && vm.user.certification.bank) {
                    vm.nextStep()
                }
            })
        } else {
            next()
        }

        // 在渲染该组件的对应路由被 confirm 前调用
        // 不！能！获取组件实例 `this`
        // 因为当守卫执行前，组件实例还没被创建
    },
    watch: {
        sel_money(val) {
            this.$router.push({ query: { money: this.sel_money, time: this.sel_time } })
            this.countMoney()
        },
        sel_time(val) {
            this.$router.push({ query: { money: this.sel_money, time: this.sel_time } })
            this.countMoney()
        }
    }
}
</script>

